/* Root Variables */
:root {
  --primary-color: #2ecc71; /* Green for text */
  --background-color: #000000; /* Black background */
  --text-color: #ffffff; /* White text */
  --secondary-text-color: #b3b3b3; /* Light grey text */
  --font-sans: 'Roboto', sans-serif;
}

/* Global Styles */
body {
  margin: 0;
  padding: 0;
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: var(--font-sans);
  overflow: hidden; /* Prevents scroll bars */
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}

/* Background Styles */
.background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #000000 0%, #1a1a1a 100%); /* Gradient background */
  z-index: -1; /* Send background to back */
}

/* Overlay Style */
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); /* Dark overlay */
}

/* Logo Styling */
.logo-container {
  padding-top: 30px;
  animation: fadeIn 1s ease-in-out; /* Fade-in effect for logo */
}

.logo {
  width: 180px; /* Increased size of the logo */
}

/* Centered Content */
.App-header {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  flex-direction: column;
  animation: fadeIn 1.5s ease-in-out;
}

.content-container {
  text-align: center;
}

.animated-title {
  font-size: 4rem;
  color: var(--primary-color); /* Green for title */
  margin-bottom: 20px;
  animation: slideIn 1s ease forwards;
  opacity: 0;
}

.tagline {
  font-size: 1.5rem;
  color: #ffffff; /* White color for tagline */
  margin-bottom: 30px;
  animation: fadeIn 1s ease-in-out 0.5s forwards; /* Fade-in with delay */
}

/* Coming Soon Button Styling */
.coming-soon-button {
  background-color: transparent; /* Hollow button */
  border: 2px solid var(--primary-color); /* Green border */
  color: var(--primary-color); /* Green text */
  padding: 15px 30px;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s, transform 0.3s;
  animation: fadeIn 1s ease-in-out 1s forwards; /* Fade-in with delay */
}

.coming-soon-button:hover {
  background-color: var(--primary-color); /* Fill color on hover */
  color: #ffffff; /* Change text color to white on hover */
  transform: scale(1.05);
}

/* Footer Styling */
.App-footer {
  padding: 20px;
  font-size: 0.9rem;
  color: var(--secondary-text-color);
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
